.SearchBlock {
    
    .SearchForm {
        background-color: #f1f6f9;
        padding-top: 20px;
        padding-bottom: 23px;
        margin-bottom: 40px;
        .content {
            .title {
                margin-bottom: 20px;
                font-family: Montserrat;
                font-size: 32px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.25;
                letter-spacing: 0.71px;
                color: #353535;
                text-align: left;
            }

            .input-group {
                input, select, button {
                    height: 46px;
                }
            }
            
            .form-control {
                width: 550px;
                border: 2px solid #f5a623;
            }
            .custom-select {
                border: 2px solid #f5a623;
            }
            .default-button {
                width: 136px;
                height: 46px;
                border: 2px solid #f5a623;
    
                span {
                    font-family: Montserrat;
                    font-size: 16px;
                    font-weight: bold;
                    font-style: normal;
                    font-stretch: normal;
                    line-height: normal;
                    letter-spacing: 0.29px;
                    text-align: center;
                    color: #ffffff;
    
                    margin: 13px 30px;
                }
            }
        }
    }
    .table {
        text-align: left;
    }
}